<template>
  <div class="pager" v-if="total>0">
    <a
      href
      class="pager-item"
      :class="{
            disabled:page === 1
        }"
      @click.prevent="handlePage(1)"
    >首页</a>
    <a
      href
      class="pager-item"
      :class="{
            disabled:page === 1
        }"
      @click.prevent="handlePage(page-1)"
    >上一页</a>
    <a
      href
      class="pager-item"
      v-for="n in numarr"
      :key="n"
      :class="{ active:n===page }"
      @click.prevent="handlePage(n)"
    >{{n}}</a>

    <a
      href
      class="pager-item"
      :class="{
            disabled:page === allPage
        }"
      @click.prevent="handlePage(page+1)"
    >下一页</a>
    <a
      href
      class="pager-item"
      :class="{
            disabled:page === allPage
        }"
      @click.prevent="handlePage(allPage)"
    >尾页</a>

    <span>
      <i>{{page}}</i>
      /
      <i>{{allPage}}</i>
    </span>
  </div>
</template>

<script>
export default {
  props: {
    page: {
      // 当前页
      default: 1,
      type: Number
    },
    total: {
      // 总页
      default: 0,
      type: Number
    },
    limit: {
      // 每页数据条数
      default: 10,
      type: Number
    },
    panelNumber: {
      //显示1-10页
      default: 10,
      type: Number
    }
  },
  computed: {
    allPage() {
      return Math.ceil(this.total / this.limit);
    },
    minNum() {
      var n = this.page - this.panelNumber / 2;
      if (n < 1) {
        n = 1;
      }
      return n;
    },
    MaxNum() {
      var n = this.minNum + this.panelNumber - 1;
      if (n > this.allPage) {
        n = this.allPage;
      }
      return n;
    },
    numarr() {
      var nums = [];
      for (var i = this.minNum; i <= this.MaxNum; i++) {
        nums.push(i);
      }
      return nums;
    }
  },
  methods: {
    handlePage(num) {
      if (num < 1) {
        num = 1;
      } else if (num > this.allPage) {
        num = this.allPage;
      }
      if (num == this.page) {
        return;
      }
      this.$emit("pageChange", num);
    }
  }
};
</script>

<style scoped>
.pager {
  text-align: center;
  margin: 20px 0;
}
.pager .pager-item {
  display: inline-block;
  padding: 5px 7px;
  border: 1px solid #ccc;
  margin: 8px;
  cursor: pointer;
  color: rgb(96, 96, 224);
}
.pager .pager-item.disabled {
  color: #ccc;
  cursor: not-allowed;
}
.pager .pager-item.active {
  cursor: auto;
  color: #f40;
  border: none;
}
</style>


