<template>
  <div>
      <div class="newsList"  v-for='item in news' :key='item.id'>
          <div class="newsImg" v-if='item.havePic'>
              <a :href="item.link" target="_blank">
                  <img :src="item.imageurls[0].url" alt="">
              </a>
          </div>
          <div v-if="item.content==''?false:true " class="news-items">
              <div class="news-wrap">
                  <h2>
                       <a :href="item.link" target="_blank">{{item.title}} </a>
                  </h2>
                  <div class="news-msg">
                      <span>{{item.channelName}}</span>
                      来源: <span>{{item.source}}</span>
                      发布日期:<span>{{item.pubDate}}</span>
                      
                  </div>
                  <p >{{item.content}}</p>
              </div>
          </div>
          
      </div>
     
  </div>
</template>

<script>
export default {
    props:{
        news:{
            type:Array,
            default:()=>[]
        }
    },
    data(){
        return{
            ist:true,
        }
    },
    methods: {
     
    },
    }

</script>

<style scoped>
.newsList{
    width: 100%;
    border-bottom: 1px solid #ccc;
    padding: 15px 0;
    display: flex;

}
.newsImg{
    width: 20%;
    height: 200px;

   
}
.newsImg a{
    width: 100%;
    height: 100%;
    display: block;
  
     display: flex;
    justify-content: start;
    align-items: center;
}
.newsImg img{  
    border: 1px solid #ccc;
    width: 80%;
    height: 120px;
    background: no-repeat center center/cover;
}
.news-items{
    width:100%;
    height: 200px;

    
    
}
.news-wrap{
    margin-top: 20px;
      height: 160px;
    width: 100%;
   
}
.news-wrap h2{

width:800px;
overflow: hidden;
text-overflow: ellipsis;

white-space: nowrap;

     font-size: 26px;
    color: cornflowerblue;
}
.news-wrap h2 a{
    color: #3498db;
}
.news-wrap p{
    margin-top: 5px;
    width: 100%;;
    height: 100px;
overflow: hidden;
text-overflow: ellipsis;
word-spacing: 2px;
white-space: pre-wrap;
line-height: 2rem;
}
.news-msg{
    margin-top: 5px;
    width: 100%;
    flex-direction: row;
  display: flex;
  color: #ccc;
  font-size: 14px;
  
}
.news-msg span{
    margin-right: 15px;
}

</style>


