<template>
    <div>
        <div class="container">
            <div class="type-title">
                 <h2>{{channelName}}</h2>
            </div>
            <NewsList :news='news' ></NewsList>
           
        </div>
        
        <Pager :page='page' :limit='limit' :total='total' :panelNumber='panelNumber' @pageChange='handlePage'></Pager>
    </div>
  
</template>

<script>
import NewsList from './newslist'
import Pager from './pager'
import * as newsServ from '../services/newservices'
export default {
    components:{
        Pager,
        NewsList
    },
    data(){
        return{
            page:1,
            channelName:'',
            limit:15,
            total:0,
            panelNumber:10,
            news:[],
        }
    },
   
    methods:{
        handlePage(newPage){
            this.page=newPage;
            this.setDatas();
        },
        async setChannelName(){
            var res = await newsServ.getNewsChannels();
            var channel= res.find((item)=>{
                return item.channelId === this.$route.params.id;
            })
            this.channelName=channel.name;
            console.log(channel);
        },
        async setDatas(){
            var res = await newsServ.getnews(this.$route.params.id,this.page,this.limit);
            this.total=res.allNum;
            this.news=res.contentlist;
            console.log(res);
        }

    },
    watch:{
        "$route.params.id":{
             immediate:true,
        handler(){
             this.setChannelName();
        this.setDatas();
        }
       
        }
    }
}
</script>

<style scoped>
.type-title{
    font-size: 2em;
    color: #888;
    border-bottom: 1px solid #ccc;
    padding: 10px;
}
</style>


